.card {
    height: 420px;
    width: 300px;
    border: 1px solid #eee;
    margin: 15px 8px;
    border-radius: 10px;
    /* overflow: hidden; */
    background-color: #fff;
    justify-content: space-between;
    overflow: hidden;
}

.card {
    position: relative;
}

.special {
    position: absolute;
    width: 356px;
    bottom: 0px;
    left: -3px;
}

.card-meta {
    color: #000;
    border-top: 1px solid #eee;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
}

.card-meta h2,p {
    margin: 2px;    
}

.item-title {
    color: #999;
    font-size: 15px;
    margin-bottom: 10px;
}

.item-desc {
    color: #333;
    font-size: 15px;
}

.card:hover {
    transform: translateY(-2px);
    
    box-shadow: rgb(4 17 29 / 25%) 0px 0px 8px 0px;
    transition: all 0.1s ease 0s;
}

.owner-box {
    border-radius: 5px;    
    margin-top: 5px;
    /* border: 1px solid #972cb0; */
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
}

.owner-address {
    margin-left: 20px;
    display: flex;
    flex-direction: column;
    align-items: start;
    color: #777;

}